<template>
    <div>
        <div class="submitfrom">
            <div class="form-group">
                <label for="id">Insert task id</label>
                <input type="text" class="form-control" id="id" required v-model="id" name="id">
            </div>
        </div>
        
        <div class="submitfrom">
            <div class="form-group">
                d<label for="desc">New Description</label>
                <textarea type="text" class="form-control" id="desc" required v-model="task.description" name="desc"></textarea>
            </div>

            <div class="form-group">
                <label for="date">Insert new date</label>
                <input type="date" class="form-control" id="date" required v-model="task.date" name="date">
            </div>

            <div class="form-group">
                <label for="pr">Priority</label>
                <b-form-select type="text" class="form-control" id="pr" required v-model="task.priority" name="pr" :options="priorityes"/>
            </div>

            <button @click="updateTask(id)" class="btn btn-success">Submit</button>
        </div>
        {{id}}
        {{task}}
    </div>
</template>
<script>
import http from '../http-common';

export default {
    name: 'edit-task',
    data () {
        return {
            task: {
                description: '',
                date: null,
                priority: ''
            },
            id: '',
            priorityes: ['Alta','Media','Baja']
        }
    }, methods: {
        refresh () {
            this.getTasks();
        },
        updateTask (id) {
            let data = JSON.stringify(this.task)
            http
                .put('/tasks/' + id, data)
                .then(res => {
                    console.log(res.data);
                })
                .catch(err => {
                    console.log(err);
                })
        }
    }
}
</script>